.contactWrapper {
    display: flex;
    height: 70vh;
}

.contactLeftBlock {
    width: 20vw;
}

.contact {
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.slogan {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.slogan h5 {
    color: #ff3946;
    margin: 10px 0;
}

.slogan h1 {
    color: #314584;
    margin: 10px 0 48px;
    font-size: 22px;
}

form {
    display: flex;
    flex-wrap: wrap;
}

input {
    width: 100%;
    border: none;
}

.email {
    background-color: #f4fafe;
    margin-top: 10px;
}

.subjectText {
    background-color: #f4fafe;
    margin-top: 10px;
}

.textArea {
    background-color: #f4fafe;
    margin-top: 10px;

    height: 15vh;
    width: 100%;
    border: none;
}

.textArea::placeholder, input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #314584;
    opacity: 1; /* Firefox */
}

.sendContactButton {
    margin-top: 20px;
    margin-right: auto;
    margin-left: auto;
    background-color: #ff3946;
    color: white;
    border: none;
    border-radius: 25px;
    height: 40px;
    width: 100%;
    padding: 0 30px;

}


@media screen and (min-width: 768px) {
    .contactWrapper {
        height: 60vh;
    }

    .slogan h1{
        font-size: 32px;
    }

    form {
        display: flex;
        justify-content: space-between;
    }
    input {
        width: 29vw;
    }

    .textArea {
        background-color: #f4fafe;
        margin-top: 40px;
        height: 15vh;
        width: 60vw;
        min-height: 80px;
    }

    .sendContactButton {
        width: auto;
        font-size: 14px;
    }

}