.footerWrapper {
    height: 30vh;
    background-color: #314584;
    display: flex;
    justify-content: center;
    border: 1px solid black;
}

.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: white;
}

.footer h5 {
    color: white;
    margin: 0;
}

.footer h2 {
    color: #ff3946;
}

.footer p{
    font-size: 10px;
}

.footerNavBar {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footerNavBar ul {
    display: flex;
    list-style-type: none;
    justify-content: center;
    align-items: center;
    padding: 0;

}

.footerNavBar li  a {
    text-decoration: none;
    color: white !important;
    font-size: 10px;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.social {
    display: flex;
    justify-content: center;
    align-items: center;
}

.social img{
    height: 3vh;
    margin: 10px 30px;
}

@media screen and (min-width: 768px){
    .footerWrapper {
        height: 40vh;
    }
    .footer {
        justify-content: center;
    }
    .footer h2 {
        font-size: 32px;
    }
    .footerNavBar {
        margin: 10px 0;
    }
    .footerNavBar li  a {
        font-size: 14px;
    }
    .footer h5 {

    }
    .social {
        margin: 10px 0;
    }
}
