.aboutUs {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 93vh;
    color: #314584;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 60vw;
}

.slogan {
    font-size: 3.0em;
    margin: 10px 0;
}

.text {
    font-size: 0.7em;
    margin: 30px 0;
    color: #7b8185;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    width: 100%;
}

.moreAboutUs {
    width: 100%;
}

.moreAboutUsBtn {
    margin-top: 0;
    background-color: #ff3946;
    color: white;
    border: none;
    border-radius: 25px;
    height: 40px;
    width: 100%;
    padding: 0 30px;
}

.container div:nth-child(2) {
    margin-top: 15px;
}

.illustration {
    display: none;
}

@media screen and (min-width: 768px) {
    .aboutUs {
        height: 90vh;
        margin-left: 20vw;
        justify-content: flex-start;
    }

    .slogan {
    }

    .title {
        width: 25vw;
        min-width: 265px;
        align-items: flex-start;
    }

    .illustration {
        display: flex;
        margin-left: 10vw;
    }

    .moreAboutUs {
        width: auto;
    }

    .moreAboutUsBtn {
        margin-right: 10px;
        width: auto;
    }

    .container {
        flex-direction: row;
        justify-content: flex-start;
    }
    .container div:nth-child(2) {
        margin-top: 0px;
    }

}