.header {
    display: flex;
    position: relative;
    height: 7vh;
    color: #314584;
}

.menuBlock {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;

}

.logoWrapper {
    display: flex;
    justify-content: flex-start;
    width: 20vw;
    order: 2;
}

.logo {
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;

}

.leftNavBar {
    display: flex;
    width: 20vw;
    position: relative;
    justify-content: flex-start;
    align-items: center;

    order: 1;
}

.menuBtn {
    display: flex;
    margin-left: 10px;
}

/*.hiddenContainer {*/
/*    display: none;*/
/*}*/

/*.menuBtn:hover .hiddenContainer {*/
/*    display: flex;*/
/*}*/

.leftNavBar ul {
    list-style-type: none;
    display: none;
    padding: 0;
}

.leftNavBar ul li {
    margin-right: 10px;
}

.leftNavBar li a {
    font-size: 14px;
    text-decoration: none;
    white-space: nowrap;
    color: #314584 !important;
    padding: 0;
    margin: 0;
}


.rightNavBar {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 60vw;
    order: 3;
}

.localization {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 0;
}

.rightNavBar .button {
    box-shadow: 0px 2px 0px 1px rgba(0, 0, 0, .1);
    background-color: white;
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ff3946;
    width: 100px;
    height: 44px;
    border-radius: 5px;
    margin-bottom: 0px;
    margin-left: 20px;
    margin-right: 25px;
}


@media screen and (min-width: 768px) {
    .header {
        height: 10vh;
    }

    .logoWrapper {
        order: 1;
        justify-content: center;
    }

    .leftNavBar {
        width: 40vw;
        min-width: 330px;
        order: 2;
    }

    .rightNavBar {
        width: 20vw;
        min-width: 250px;
        order: 3;
    }

    .menuBtn {
        display: none;
    }

    .leftNavBar ul {
        display: flex;
    }

    .rightNavBar .button {
        width: 150px;
    }
}


