.worksWrapper {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.leftBlock {
    width: 20vw;
}

.works {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.worksTitle {
    font-size: 1.5em;
    color: #314584;
    margin-bottom: 20px;
}

.worksExamples {
    display: flex;
    flex-wrap: wrap;
    width: 60vw;
    justify-content: center;
}

.seeMoreText {
    color: #ff3946;
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
    .works {
        min-height: 90vh;
    }

    .worksTitle {
        font-size: 2em;
    }

    .worksWrapper {
        height: 100vh;
    }
    .worksExamples {
        justify-content: space-between;
        height: 60vh;
    }

}